/* forum-regular - cyrillic */
@font-face {
  font-family: "Montserrat";
  font-style: bold;
  font-weight: 700;
  src: local("Montserrat bold"), url("../fonts/montserrat/Montserrat-Bold.woff2") format("woff2"), url("../fonts/montserrat/Montserrat-Bold.woff") format("woff");
  /* Modern Browsers */
  font-display: swap; }

@font-face {
  font-family: "Lora-BoldItalic";
  font-style: bold italic;
  font-weight: 700;
  src: local("Lora bold italic"), url("../fonts/lora/Lora-BoldItalic.woff2") format("woff2"), url("../fonts/lora/Lora-BoldItalic.woff") format("woff");
  /* Modern Browsers */
  font-display: swap; }

@font-face {
  font-family: "Lora Italic";
  font-style: italic;
  font-weight: 400;
  src: local("Lora-Italic"), url("../fonts/lora/Lora-Italic.woff2") format("woff2"), url("../fonts/lora/Lora-Italic.woff") format("woff");
  /* Modern Browsers */
  font-display: swap; }

@font-face {
  font-family: "Lora";
  font-style: Regular;
  font-weight: 700;
  src: local("Lora-Regular"), url("../fonts/lora/Lora-Regular.woff2") format("woff2"), url("../fonts/lora/Lora-Regular.woff") format("woff");
  /* Modern Browsers */
  font-display: swap; }

#icon-logo path {
  fill: #5E4338; }

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 100%; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

body {
  min-width: 320px;
  height: 100%;
  margin: 0;
  padding: 0;
  color: #1A120F;
  background-color: #f5f5f5;
  font-family: "Lora", "Times New Roman", serif;
  font-weight: 400;
  font-size: 24px;
  font-style: normal;
  line-height: 30px; }

a {
  text-decoration: none;
  color: #f5f5f5; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0 0 0 0);
  border: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%); }

.button {
  color: #5E4338;
  background-color: #e0e0e0;
  text-align: center;
  border-radius: 50%;
  border: none;
  cursor: pointer; }

.page-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  min-height: 100%; }

.body--modal-open {
  height: 100vh;
  overflow: hidden; }

.page-footer {
  width: 100%;
  -webkit-box-flex: 0;
      -ms-flex: 0 0 auto;
          flex: 0 0 auto; }
